#tentang {
  width: 100%;
  height: 115vh;
  display: flex;
  background-color: var(--black-color);
  
}
 .isi {
  display: flex;
  flex-direction: column;
  width: 80%;
  margin: 5% 10% 0;
  border-radius: 14px;
  -webkit-border-radius: 14px;
  -moz-border-radius: 14px;
  -ms-border-radius: 14px;
  -o-border-radius: 14px;
}
 .isi .titleA {
  position: relative;
  width: 100%;
  margin-top: 4%;
  text-align: start;
}
 .isi .titleA h1 {
  position: relative;
  letter-spacing: 2px;
  color: #1abc9c;
  font-weight: 700;
  font-size: 38px;
}
 .isi .titleA h1::after {
  content: "";
  width: 6em;
  height: 4px;
  left: 0.4em;
  bottom: -10px;
  position: absolute;
  border-radius: 8px;
  background-color: rgb(236, 230, 230);
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  -ms-border-radius: 8px;
  -o-border-radius: 8px;
}
.isi .isiTentang {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  margin: 7% auto;
  height: 60vh;
}
.isi .isiTentang .teksTentang {
  height: 330px;
  width: 600px;
  margin: auto;
  color: var(--sky-color);
  text-align: center;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}
.isi .isiTentang .teksTentang img {
  height: 80px;
  width: 300px;
  margin-top: 30px;
}
.isi .isiTentang .teksTentang p {
  margin: 3% auto;
  width: 60%;
  font-weight: 450;
  letter-spacing: 1px;
}

.elemenTentang {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: center;
  flex-wrap:nowrap;

}

