.linkHero {
  width: 100%;
  height: 11vh;
  background-color: var(--black-color);
  display: flex;
  justify-content: center;
  color: #fff;
  align-items: center;
  flex-direction: column;
}

.linkList {
  width: 80%;
  height: 15vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;

  ul {
    display: flex;
    flex-direction: row;
    width: 60%;
    justify-content: space-evenly;
  }

  li {
    display: flex;
    position: relative;
    list-style-type: none;
    margin: 4px;
    justify-content: space-evenly;

    p {
      opacity: 0.6;
      letter-spacing: 1px;
    }

    a {
      color: rgb(241, 234, 234);
      font-size: 14px;
      position: relative;
      text-transform: uppercase;
      text-decoration: none;
      font-weight: 500;
      letter-spacing: 1px;
      padding-top: 5px;
      text-transform: uppercase;
      text-align: center;
      align-items: center;
      cursor: pointer;
      opacity: 0.4;
      transition: all 0.6s ease-in-out;
      -webkit-transition: all 0.6s ease-in-out;
      -moz-transition: all 0.6s ease-in-out;
      -ms-transition: all 0.6s ease-in-out;
      -o-transition: all 0.6s ease-in-out;

      &::after {
        content: "";
        position: absolute;
        width: 25%;
        bottom: -1px;
        left: 40%;
        opacity: 0.4;
        height: 2.4px;
        border-radius: 8px;
        background: #1abc9c;
        transition: 0.5s ease-out;
        -webkit-transition: 0.5s ease-out;
        -moz-transition: 0.5s ease-out;
        -ms-transition: 0.5s ease-out;
        -o-transition: 0.5s ease-out;
        -webkit-border-radius: 8px;
        -moz-border-radius: 8px;
        -ms-border-radius: 8px;
        -o-border-radius: 8px;
      }

      &:hover::after {
        left: 0;
        width: 100%;
        opacity: 1;
      }

      &:hover {
        opacity: 1;
      }
    }
  }
}

.line {
  color: rgba(255, 255, 255, 0.685);
  cursor: default;
}
